@media only screen and (max-width: 768px) and (max-height: 1024px) {
    .each-fade {
        height: 600px !important
    }
}
@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes load {
  0% {
    opacity: 0.08;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    letter-spacing: 3px;
  }
}

@keyframes load {
  0% {
    opacity: 0.08;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    letter-spacing: 3px;
  }
}

.transition-footer-top {
  bottom: 87.5% !important;
  -webkit-transition: 1.75s;
  margin-bottom: 0px !important;
}

.transition-footer-bottom {
  bottom: 0% !important;
  -webkit-transition: 1.25s;
  margin-bottom: 0px !important;
}

.transition-container-top {
  top: 9% !important;
  -webkit-transition: 1.75s;
  margin-bottom: 0px !important;
}

.transition-container-bottom {
  top: 100% !important;
  -webkit-transition: 1.25s;
  height: 0 !important;
  margin-bottom: 0px !important;
}

.transition-container-hide {
  display: none !important;
}

